<template>
  <v-card height="100%">
    <v-card-text>
      <component
        v-if="component"  
        :is="component"
        :item="item" 
      />
    </v-card-text>
  </v-card>
</template>
<script>
import { mapActions } from 'vuex'
export default {
  name: 'ViewItem',
  data  () {
    return {
      component: null,
      loading: false, 
      item: null
    }
  },
  computed: {
    id () {
      return this.$route.params.id
    },
    collection () {
      return this.$route.params.collection
    }
  },
  watch: {
    '$route': {
      handler: 'updateView',
      immediate: true
    }
  },
  methods: {
    ...mapActions({
      get: 'api/get'
    }),
    _snake2Pascal( str ){
        str +='';
        str = str.split('_');
        for(var i=0;i<str.length;i++){ 
            str[i] = str[i].slice(0,1).toUpperCase() + str[i].slice(1,str[i].length);
        }
        return str.join('');
    },
    async updateView () {
      await this.getComponent()
      await this.fetchItem()
    },
    async getComponent () {
      const { collection, _snake2Pascal } = this
      const componentName = _snake2Pascal(collection)
      const component = await import('../components/views/' + componentName)
      this.component = component.default
    
    },
    async fetchItem ( ) {
      let { collection, id } = this
      this.loading = true

      const result = await this.get({ collection: collection + '/' + id, data: {

      } })
      console.log(result.data);
      this.item = result.data
   

      this.loading = false
     
    }
  }
}
</script>